/*Bootstrap Reset*/
body {
    background: #f4f2f3;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    color: #0E0D0F;
}

a {
    color: #006D77;

    &:hover,
    &:focus {
        color: #013338;
    }
}

.form-control {
    background: #83C5BE;
    height: 50px;
    border: none;
    border-radius: 0;
    padding: 10px 20px;
    font-size: 16px;
    text-transform: uppercase;
    color: #343434;

    &::-webkit-input-placeholder {
        /* Chrome/Opera/Safari */
        color: #343434;
        opacity: 1;
    }

    &::-moz-placeholder {
        /* Firefox 19+ */
        color: #343434;
        opacity: 1;
    }

    &:-ms-input-placeholder {
        /* IE 10+ */
        color: #343434;
        opacity: 1;
    }

    &:-moz-placeholder {
        /* Firefox 18- */
        color: #343434;
        opacity: 1;
    }
}

.btn-primary {
    background: #006D77;
    border-color: #006D77;

    &:hover,
    &:focus {
        background: #527A7E;
        border-color: #527A7E;
    }

    &:focus {
        box-shadow: 0 0 0 .2rem #7BA0A4;
    }

    &:active {
        background: #31666B;
    }
}

.btn-primary:not(:disabled):not(.disabled).active,
.btn-primary:not(:disabled):not(.disabled):active,
.show>.btn-primary.dropdown-toggle {
    background-color: #1C4F48;
    border-color: #1C4F48;
}

.btn-primary:not(:disabled):not(.disabled).active:focus,
.btn-primary:not(:disabled):not(.disabled):active:focus,
.show>.btn-primary.dropdown-toggle:focus {
    box-shadow: 0 0 0 .2rem #679891;
}

.container {
    @media(min-width: 1660px) {
        max-width: 1630px;
    }
}

.wider-section {
    margin: 0 auto;

    @media(min-width: 1750px) {
        max-width: 1720px;
    }
}

.wider-container {
    @media(min-width: 1750px) {
        max-width: 1720px;
    }
}

img {
    max-width: 100%;
}

a,
button,
input {

    &:focus,
    &:active {
        outline: 0;
    }
}

.main-wrapper {
    overflow: hidden;
    position: relative;
}

/*Header*/
.site-header-row {
    @media(max-width: 767px) {
        padding: 7px 0;
