        align-items: center;
        margin-bottom: 10px;
    }

    @media(min-width: 768px) {
        width: auto;
        -ms-flex: inherit;
        flex: inherit;
        max-width: inherit;
    }

}

.mobile-main-nav-toggler {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 0;
    padding: 0;
    background: transparent;
    width: 40px;
    height: 40px;

    span {
        height: 2px;
        width: 32px;
        background: #0E0D0F;

        &:not(:last-child) {
            margin-bottom: 6px;
        }

    }

    &:hover,
    &:focus {
        outline: none;
    }

    &:active {
        span {
            background: #006D77;
        }
    }
}

a.header-logo-wrap {

    &:hover,
    &:focus {
        text-decoration: none;
        opacity: 0.75;

        .header-logo-text {
            color: #4d4d4d;
