        flex: inherit;
        max-width: inherit;
        margin: 0 50px;
    }

    @media(min-width: 768px) and (max-width: 1659px) {
        margin-top: 15px;
        order: 2;
        min-width: 100%;
        max-width: 100%;
    }
}

#header-main-navigation {
    @media(min-width: 768px) {
        display: flex;
    }
}

.header-main-navigation {
    @media(max-width: 767px) {
        margin: 0 -15px;

        &>ul {
            padding-bottom: 10px;
        }

        ul li.active {
            a {
                background: #006D77;
                color: #fff;
            }
        }

        ul a {
            display: block;
            padding: 10px 15px;

            &:hover,
            &:focus {
                text-decoration: none;
            }

            &:active {
                background: #006D77;
                color: #fff;
            }
        }
    }

    @media(min-width: 768px) {
        position: relative;

        &>ul {
            display: flex;
            align-items: center;
            flex-wrap: wrap;

            &>li {
                &:not(:last-child) {
                    margin-right: 50px;
                }

                &>a {

                    &:hover,
                    &:focus {
                        color: #006D77;
                    }
                }
            }
        }

        @media(min-width: 1660px) {
            &:before {
                content: '';
                position: absolute;
                left: -16px;
                top: 50%;
                transform: translate(0, -50%);
                width: 2px;
                height: 40px;
                background: #006D77;
            }
        }
    }

    ul {
        list-style: none;
        padding-left: 0;
        margin-bottom: 0;
    }

    ul li a {
        font-family: 'Oswald';
        color: #0E0D0F;
        text-transform: uppercase;
    }

    &>ul>li:not(:last-child) {
        @media(min-width: 768px) and (max-width: 991px) {
            margin-right: 20px;
        }

    }
}

